import Vue from 'vue';
import i18n from '../../../i18n';
import { emojilist } from '../../../../../misc/emojilist';
import { getStaticImageUrl } from '../../../common/scripts/get-static-image-url';
import { faAsterisk, faLeaf, faUtensils, faFutbol, faCity, faDice, faGlobe, faHistory } from '@fortawesome/free-solid-svg-icons';
import { faHeart, faFlag } from '@fortawesome/free-regular-svg-icons';
import { groupByX } from '../../../../../prelude/array';
export default Vue.extend({
    i18n: i18n('common/views/components/emoji-picker.vue'),
    data() {
        return {
            emojilist,
            getStaticImageUrl,
            customEmojis: {},
            faGlobe, faHistory,
            categories: [{
                    text: this.$t('custom-emoji'),
                    icon: faAsterisk,
                    isActive: true
                }, {
                    name: 'people',
                    text: this.$t('people'),
                    icon: ['far', 'laugh'],
                    isActive: false
                }, {
                    name: 'animals_and_nature',
                    text: this.$t('animals-and-nature'),
                    icon: faLeaf,
                    isActive: false
                }, {
                    name: 'food_and_drink',
                    text: this.$t('food-and-drink'),
                    icon: faUtensils,
                    isActive: false
                }, {
                    name: 'activity',
                    text: this.$t('activity'),
                    icon: faFutbol,
                    isActive: false
                }, {
                    name: 'travel_and_places',
                    text: this.$t('travel-and-places'),
                    icon: faCity,
                    isActive: false
                }, {
                    name: 'objects',
                    text: this.$t('objects'),
                    icon: faDice,
                    isActive: false
                }, {
                    name: 'symbols',
                    text: this.$t('symbols'),
                    icon: faHeart,
                    isActive: false
                }, {
                    name: 'flags',
                    text: this.$t('flags'),
                    icon: faFlag,
                    isActive: false
                }]
        };
    },
    created() {
        let local = (this.$root.getMetaSync() || { emojis: [] }).emojis || [];
        local = groupByX(local, (x) => x.category || '');
        this.customEmojis = local;
        if (this.$store.state.device.activeEmojiCategoryName) {
            this.goCategory(this.$store.state.device.activeEmojiCategoryName);
        }
    },
    methods: {
        go(category) {
            this.goCategory(category.name);
        },
        goCategory(name) {
            let matched = false;
            for (const c of this.categories) {
                c.isActive = c.name === name;
                if (c.isActive) {
                    matched = true;
                    this.$store.commit('device/set', { key: 'activeEmojiCategoryName', value: c.name });
                }
            }
            if (!matched) {
                this.categories[0].isActive = true;
            }
        },
        chosen(emoji) {
            const getKey = (emoji) => emoji.char || `:${emoji.name}:`;
            let recents = this.$store.state.device.recentEmojis || [];
            recents = recents.filter((e) => getKey(e) !== getKey(emoji));
            recents.unshift(emoji);
            this.$store.commit('device/set', { key: 'recentEmojis', value: recents.splice(0, 16) });
            this.$emit('chosen', getKey(emoji));
        }
    }
});
